.table {
	margin-bottom: rem(18);
	border-bottom: rem(2) solid var(--c-yellow);

	&__row {
		padding: 0 rem(15);
		height: rem(58);
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		background-color: white;

		&:nth-child(2n) {
			background-color: var(--c-gray5);
		}

		div {
			display: flex;
			&:first-child {
				// width: rem(340);
				flex: 0 0 rem(340);
			}
			&:nth-child(2) {
				flex: 0 0 rem(160);
			}
			&:nth-child(3) {
				flex: 0 0 rem(168);
			}
			&:last-child {
				flex-grow: 1;
			}

			.field_quantity {
				max-width: rem(87);
			}

			b {
				font-weight: 600;
				font-size: rem(16);
			}

			.cart_item_delete {
				margin-left: auto;

				i {
					font-size: rem(16);
				}
			}
		}
	}
}

.table_result {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	.button_default {
		height: auto;
	}

	b {
		font-weight: 600;
	}
}

.form_message {
	margin-top: rem(50);
	margin-bottom: rem(70);

	display: flex;
	flex-wrap: wrap;

	background-color: var(--c-gray5);
	padding: rem(50);

	&__left, &__right {
		flex: 0 0 50%;
	}
	&__left {
		padding-right: rem(18);
	}
	&__right {
		padding-left: rem(18);
		display: flex;
		flex-direction: column;

		textarea.field {
			height: rem(256);
			resize: none;
		}

		.button_box {
			margin-top: auto;
		}
	}
}

.field_set {
	margin-bottom: rem(18);
	&:last-child {
		margin-bottom: 0;
	}
	label {
		font-size: rem(16);
		display: flex;
		align-items: flex-start;
		margin-bottom: rem(7);

		span {
			margin-left: rem(2);
			color: var(--c-yellow);
		}
	}

	.captcha {
		display: flex;
		// flex-wrap: wrap;

		&__figure {
			flex: 0 0 rem(200);
			height: rem(54);
			margin-right: rem(15);
		}

		.field {
			// flex: 0 0 auto;
		}
	}
}